import firebase from 'firebase'
import 'firebase/firestore'

// firebase init goes here
const config = {
  apiKey: 'AIzaSyDvlxGox2E7xzj544NNoihAVS1AONvYpwc',
  authDomain: 'pwa2018-58147.firebaseapp.com',
  databaseURL: 'https://pwa2018-58147.firebaseio.com',
  projectId: 'pwa2018-58147',
  storageBucket: 'pwa2018-58147.appspot.com',
  messagingSenderId: '921492286253'

}
firebase.initializeApp(config)

// firebase utils
const db = firebase.firestore()
const auth = firebase.auth()
const currentUser = auth.currentUser

// date issue fix according to firebase
const settings = {
  timestampsInSnapshots: true
}
db.settings(settings)

// firebase collections
const usersCollection = db.collection('users')
const postsCollection = db.collection('posts')
const commentsCollection = db.collection('comments')
const likesCollection = db.collection('likes')

export {
  db,
  auth,
  currentUser,
  usersCollection,
  postsCollection,
  commentsCollection,
  likesCollection
}



